    }
}

@media only screen and (min-width: 240px) and (max-width: 479.98px) {
    .sub-box {
        width: calc(50% - .4em);
        margin-left: 0;
        margin-right: .5em;
    }

    .sub-box:nth-child(2n) {
        margin-right: 0;
    }
}
</style>